export default {
	data() {
		return {
			show: false,
			video: ''
		};
	},
	methods: {
		//上传视频
		isShow() {
			uni.showLoading({
				title:'上传中'
			})
			uni.chooseVideo({
				count: 1,
				sourceType: ['camera', 'album'],
				maxDuration:20,
			}).then(res=>{
				console.log(res)
				this.video=res[1].tempFilePath;
				this.show = true;
				uni.hideLoading()
			}).catch(err=>{
				console.log((err))
			})
		},
		//删除视频
		deleteVideo(){
			this.video=''
			this.show=false
		}
	}
};
